.myinfo-bounce {
  -webkit-animation: myinfo-bounce-in 5s;
          animation: myinfo-bounce-in 5s;
}

.myinfo-bounce-leave-active {
  animation: myinfo-bounce-in 3s reverse;
}

@-webkit-keyframes myinfo-bounce-in {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1.3, 1.3);
            transform: scale(1.3, 1.3);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes myinfo-bounce-in {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1.3, 1.3);
            transform: scale(1.3, 1.3);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

